@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: 'Lora', serif;
  width: 100%;
  height: 100%;
}

html,
#root,
.App {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
  width: 100%;
  height: 65px;
  background-color: #000;
  color: #fff;
  display: flex;
}

.LinkedinIcon{
  padding: 10px;
}

.GithubIcon{
  padding: 10px;
}

.Nav {
  float: right;
  display: flex;
  position: absolute;
  right: 20px;
}

.NavItem {
  width: 80px;
  padding: 10px;
  margin: 10px;
  text-align: center;
  cursor: pointer;
}

.NavItem.active {
  border-bottom: 2px solid #fff;
}

/* body part */
.BodyPart {
  height: calc(100% - 65px);
  width: 100%;
}

.Profile {
  height: 100%;
  display: flex;
}

.leftSide,
.rightSide {
  width: 50%;
  align-items: center;
  justify-content: center;
  display: flex;
}

.leftSide {
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: #3f565d;
  color: #fff;
}

.leftSide .title {
  width: 80%;
  margin-left: 10%;
  font-size: 58px;
  font-weight: 700;
  margin-bottom: 36px;
}

.leftSide .subtitle {
  font-size: 24px;
  font-weight: 500;
  width: 80%;
  margin-left: 10%;
}

.rightSideContent {
  width: 70%;
  margin-left: 15%;
}

.rightSide .title {
  width: auto;
  font-size: 24px;
  font-weight: 500;
  display: inline-block;
  border-bottom: 4px solid #3f565d;
  padding-bottom: 4px;
}

/* .rightSide .work{
  height: 70%;
} */

.expDiv {
  margin: 10px 10px 40px 10px;
}

.expDiv .companyName {
  font-size: 18px;
  font-weight: 400;
}

.expDiv .location {
  font-size: 12px;
  margin-left: 8px;
}

.expDiv .position {
  font-size: 16px;
}

.expDiv .time {
  font-size: 12px;
  color: #3f565d;
  margin-left: 8px;
}

/* Project */
.Project {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
}

.comboDiv {
  display: flex;
  width: 90%;
  height: 60%;
  max-height: 450px;
  max-width: 1390px;
}

.projectDiv {
  width: 25%;
  color: #fff;
  position: relative;
  padding: 12px;
}

.projectDiv .name {
  margin-top: 26px;
  font-weight: bold;
}

.projectDiv .description {
  margin-top: 24px;
  line-height: 1.5;
  font-size: 14px;
}

.projectDiv .link {
  text-align: center;
  width: 65px;
  position: absolute;
  bottom: 15px;
  left: 12px;
  border: 2px solid #fff;
  text-decoration: none;
  cursor: pointer;
  color: #fff;
}

.projectDiv .link:hover {
  border: 2px solid #000;
  background-color: #fff;
  color: #000;
}

.projectDiv .tech {
  background-color: #fff;
  display: inline-block;
  position: absolute;
  top: 16px;
  right: 12px;
  width: 36px;
  height: auto;
  border-radius: 18px;
}

.projectDiv .tech img {
  width: 36px;
  height: auto;
}

.projectDiv:nth-child(1) {
  background-color: #23272c;
}

.projectDiv:nth-child(2) {
  background-color: #4b4174;
}

.projectDiv:nth-child(3) {
  background-color: #2a2a48;
}

.projectDiv:nth-child(4) {
  background-color: #9292a8;
}

/* Demo */
.Demo {
  width: 100%;
  height: 100%;
  display: flex;
}


.Demo .leftSide {
  color: #000;
  flex-direction: column;
  background-color: #fff;
  width: 35%;
}

.Demo .rightSide {
  background-color: #1f7d7e;
  flex-direction: column;
  width: 65%;
  align-items: flex-start;
}

.Demo .title{
  color: #fff;
  font-size: 48px;
  width: 70%;
  margin-left: 10%;
  display: inline-block;
  border: none;
  padding-bottom: 60px;
  font-weight: bold;
}

.Demo .subtitle {
  max-width: 700px;
  min-width: 500px;
  color: #fff;
  margin-left: 10%;
  font-size: 22px;
  font-weight: 300;
}

/* .herokuLogo{
  margin-top: 20px;
} */
.demoDiv{
  width: 200px;
  height: 64px;
  text-align: center;
  margin-top: 20px;
}

.demoDiv a{
  text-decoration: none;
  font-size: 24px;
  color: #000;
  font-weight: bold;
  height: 40px;
}

.demoDiv a:hover{
  border-bottom: 4px solid #1f7d7e;
}

.herokuLogo{
  margin-bottom: 100px;
}


